// request interceptor
service.interceptors.request.use(
  config => {
    console.log(config)
    if(config.url == "/history/ecgExport"){
      config.responseType = 'blob';
    }
    console.log(store.getters["login/newToken"]);
    if (store.getters["login/newToken"]) {
      //判断token
      config.headers["token"] = getToken();
    }
    if (
      config.method == "post" ||
      config.method == "put" ||
      config.method == "delete"
    ) {
      if (
        config.url == "/monitor/patientRule" ||
        config.url == "/monitor/setRule" ||
        config.url == "/hospital/updateHospital" ||
        config.url == "/hospital/room" ||
        config.url == '/repeater/room'
      ) {
        if (config.method == "delete") {
          config.headers["Content-Type"] = "application/x-www-form-urlencoded";
          config.data = Qs.stringify(config.data);
        } else {
          config.headers["Content-Type"] = "application/json";
        }
      } else {
        config.headers["Content-Type"] = "application/x-www-form-urlencoded";
        config.data = Qs.stringify(config.data);
      }
    } else {
      config.headers["Content-Type"] = "application/json";
    }
    console.log(config);
    return config;
  },
  error => {
    // do something with request error
    console.log(error); // for debug
    return Promise.reject(error);
  }
);

