<script>
import { rule, setRule } from "@/api/monitor";
import popUp from "../../components/popUp";
import { mapGetters, mapActions, mapState, mapMutations } from "vuex";
var that = {};
export default {
  name: "szgz",
  data() {
    return {
      selectList: [
        this.$t("monitor.temp"),
        this.$t("monitor.xl"),
        this.$t("monitor.hxl"),
        this.$t("monitor.xy")
      ],
      selectIndex: 0,
      selectTems: true,
      wendu: "(℃)",
      width: 56,
      unusualList: {
        twList: {
          normalHigh: 37.0,
          unusualLow: 34.0,
          unusualHigh: 38.0,
          alertLow: 32.0,
          alertHigh: 42.0,
          alertStatus: true,
          twEdit: false
        },
        xlList: {
          unusualLow: 45,
          normalHigh: 110,
          unusualHigh: 130,
          alertLow: 40,
          alertHigh: 140,
          alertStatus: true,
          xlEdit: false
        },
        hxList: {
          unusualLow: 15,
          normalHigh: 25,
          unusualHigh: 40,
          alertLow: 10,
          alertHigh: 60,
          alertStatus: true,
          hxEdit: false
        },
        xyList: {
          normalLow: 90,
          unusualLow: 91,
          alertLow: 50,
          alertStatus: true,
          xyEdit: false
        }
      },
      accecptDataList: [], //当前科室规则
      defaultList: [] //默认规则
    };
  },
  props: ["szgzStatus", "transData"],
  beforeCreate() {
    that = this;
  },
  computed: {
    ...mapGetters("monitor", ["departmentId"]),
    ...mapState("monitor", ["rulList", "selectTem"])
  },
  mounted() {
    console.log(this.transData);
  },
  watch: {
    selectTems(o, n) {
      if (o) {
        that.unusualList.twList.normalHigh = that.zhuanhuaHua(
          that.unusualList.twList.normalHigh
        );
        that.unusualList.twList.unusualLow = that.zhuanhuaHua(
          that.unusualList.twList.unusualLow
        );
        that.unusualList.twList.unusualHigh = that.zhuanhuaHua(
          that.unusualList.twList.unusualHigh
        );
        that.unusualList.twList.alertHigh = that.zhuanhuaHua(
          that.unusualList.twList.alertHigh
        );
        that.unusualList.twList.alertLow = that.zhuanhuaHua(
          that.unusualList.twList.alertLow
        );
      } else {
        that.unusualList.twList.normalHigh = Number(
          that.zhuanHuaShe(that.unusualList.twList.normalHigh)
        );
        that.unusualList.twList.unusualLow = Number(
          that.zhuanHuaShe(that.unusualList.twList.unusualLow)
        );
        that.unusualList.twList.unusualHigh = Number(
          that.zhuanHuaShe(that.unusualList.twList.unusualHigh)
        );
        that.unusualList.twList.alertHigh = Number(
          that.zhuanHuaShe(that.unusualList.twList.alertHigh)
        );
        that.unusualList.twList.alertLow = Number(
          that.zhuanHuaShe(that.unusualList.twList.alertLow)
        );
      }
    }
  },
  methods: {
    ...mapActions("monitor", ["refreshData", "getRul"]),
    ...mapMutations("monitor", ["changeselectTem"]),
    changeSelect(index) {
      //选择设置规则
      this.selectIndex = index;
    },
    showSet() {
      this.$emit("closeSz", "设置");
    },
    xuanZeWen(data, canEdit) {
      if (!canEdit) {
        if (data) {
          this.wendu = "(℃)";
        } else {
          this.wendu = "(℉)";
        }
        this.selectTems = data;
      }
    },
    chooseBedType() {
      if (
        !this.unusualList.twList.unusualLow ||
        this.unusualList.twList.alertLow >
          Number((Number(this.unusualList.twList.unusualLow) + 0.1).toFixed(1))
      ) {
        that.error(that.$t("monitor.err.twdyw"));
      } else if (
        !this.unusualList.twList.unusualHigh ||
        !this.unusualList.twList.normalHigh ||
        this.unusualList.twList.alertHigh < this.unusualList.twList.normalHigh
      ) {
        that.error(that.$t("monitor.err.twgyw"));
      } else if (
        !this.unusualList.xlList.unusualLow ||
        this.unusualList.xlList.alertLow >
          this.unusualList.xlList.unusualLow + 1
      ) {
        that.error(that.$t("monitor.err.xldyw"));
      } else if (
        !this.unusualList.xlList.normalHigh ||
        !this.unusualList.xlList.unusualHigh ||
        this.unusualList.xlList.alertHigh < this.unusualList.xlList.normalHigh
      ) {
        that.error(that.$t("monitor.err.xlgyw"));
      } else if (
        !this.unusualList.hxList.unusualLow ||
        this.unusualList.hxList.alertLow >
          this.unusualList.hxList.unusualLow + 1
      ) {
        that.error(that.$t("monitor.err.hxdyw"));
      } else if (
        !this.unusualList.hxList.normalHigh ||
        !this.unusualList.hxList.unusualHigh ||
        this.unusualList.hxList.alertHigh < this.unusualList.hxList.normalHigh
      ) {
        that.error(that.$t("monitor.err.hxgyw"));
      } else if (
        !this.unusualList.xyList.normalLow ||
        !this.unusualList.xyList.unusualLow
      ) {
        that.error(that.$t("monitor.err.xydyw"));
      } else {
        var arrList = [];
        console.log(that.accecptDataList);
        that.accecptDataList.map(item => {
          if (item.type == 0) {
            var obj = {};
            obj.departmentId = item.departmentId;
            obj.type = item.type;
            if (!this.selectTems) {
              obj.paramOne = that.zhuanhuaHua(
                that.unusualList.twList.normalHigh
              );
              obj.paramTwo = that.zhuanhuaHua(
                that.unusualList.twList.unusualLow
              );
              obj.paramThree = that.zhuanhuaHua(
                that.unusualList.twList.unusualHigh
              );
              obj.highAlert = that.zhuanhuaHua(
                that.unusualList.twList.alertHigh
              );
              obj.lowAlert = that.zhuanhuaHua(that.unusualList.twList.alertLow);
            } else {
              obj.paramOne = that.unusualList.twList.normalHigh;
              obj.paramTwo = that.unusualList.twList.unusualLow;
              obj.paramThree = that.unusualList.twList.unusualHigh;
              obj.highAlert = that.unusualList.twList.alertHigh;
              obj.lowAlert = that.unusualList.twList.alertLow;
            }
            obj.alertFlag = Number(that.unusualList.twList.alertStatus);
            arrList.push(obj);
          } else if (item.type == 1) {
            var obj = {};
            obj.departmentId = item.departmentId;
            obj.type = item.type;
            obj.paramOne = that.unusualList.xlList.normalHigh;
            obj.paramTwo = that.unusualList.xlList.unusualLow;
            obj.paramThree = that.unusualList.xlList.unusualHigh;
            obj.highAlert = that.unusualList.xlList.alertHigh;
            obj.lowAlert = that.unusualList.xlList.alertLow;
            obj.alertFlag = Number(that.unusualList.xlList.alertStatus);
            arrList.push(obj);
          } else if (item.type == 2) {
            var obj = {};
            obj.departmentId = item.departmentId;
            obj.type = item.type;
            obj.paramOne = that.unusualList.hxList.normalHigh;
            obj.paramTwo = that.unusualList.hxList.unusualLow;
            obj.paramThree = that.unusualList.hxList.unusualHigh;
            obj.highAlert = that.unusualList.hxList.alertHigh;
            obj.lowAlert = that.unusualList.hxList.alertLow;
            obj.alertFlag = Number(that.unusualList.hxList.alertStatus);
            arrList.push(obj);
          } else if (item.type == 3) {
            var obj = {};
            obj.departmentId = item.departmentId;
            obj.type = item.type;
            obj.paramOne = that.unusualList.xyList.normalLow;
            obj.paramTwo = that.unusualList.xyList.unusualLow;
            obj.lowAlert = that.unusualList.xyList.alertLowt;
            obj.alertFlag = Number(that.unusualList.xyList.alertStatus);
            arrList.push(obj);
          }
        });
        console.log(arrList);
        var transId = "";
        if (this.transData) {
          transId = this.transData.patientId;
        }
        // if (this.transData) {
        //   setPatientRule({
        //     patientId: this.transData.patientId,
        //     list: arrList
        //   }).then(res => {
        //     console.log(res);
        //     if (res.code == 200 && res.success) {
        //       that.$emit("closeSz", "设置");
        //       console.log(that.selectTems);
        //       that.changeselectTem(that.selectTems);
        //       that.refreshData();
        //     }
        //   });
        // } else {
        //   setRule({
        //     list: arrList
        //   }).then(res => {
        //     console.log(res);
        //     if (res.code == 200 && res.success) {
        //       that.$emit("closeSz", "设置");
        //       console.log(that.selectTems);
        //       that.changeselectTem(that.selectTems);
        //       that.refreshData();
        //     }
        //   });
        // }
        setRule({
          patientId: transId,
          departmentId: this.departmentId,
          list: arrList
        }).then(res => {
          console.log(res);
          if (res.code == 200 && res.success) {
            that.$emit("closeSz", "设置");
            console.log(that.selectTems);
            that.changeselectTem(that.selectTems);
            that.refreshData();
          }
        });
      }
    },
    selectDefaultValue(data) {
      console.log(data);
      if (data == "体温") {
        if (!this.selectTems) {
          that.unusualList.twList.normalHigh = Number(
            this.zhuanHuaShe(that.defaultList[0].paramOne)
          );
          that.unusualList.twList.unusualLow = Number(
            this.zhuanHuaShe(that.defaultList[0].paramTwo)
          );
          that.unusualList.twList.unusualHigh = Number(
            this.zhuanHuaShe(that.defaultList[0].paramThree)
          );
          that.unusualList.twList.alertHigh = Number(
            this.zhuanHuaShe(that.defaultList[0].highAlert)
          );
          that.unusualList.twList.alertLow = Number(
            this.zhuanHuaShe(that.defaultList[0].lowAlert)
          );
          // that.unusualList.twList.alertStatus = that.defaultList[0].alertFlag.toString();
        } else {
          that.unusualList.twList.normalHigh = that.defaultList[0].paramOne;
          that.unusualList.twList.unusualLow = that.defaultList[0].paramTwo;
          that.unusualList.twList.unusualHigh = that.defaultList[0].paramThree;
          that.unusualList.twList.alertHigh = that.defaultList[0].highAlert;
          that.unusualList.twList.alertLow = that.defaultList[0].lowAlert;
          // that.unusualList.twList.alertStatus = that.defaultList[0].alertFlag.toString();
        }
      } else if (data == "心率") {
        that.unusualList.xlList.normalHigh = that.defaultList[1].paramOne;
        that.unusualList.xlList.unusualLow = that.defaultList[1].paramTwo;
        that.unusualList.xlList.unusualHigh = that.defaultList[1].paramThree;
        that.unusualList.xlList.alertHigh = that.defaultList[1].highAlert;
        that.unusualList.xlList.alertLow = that.defaultList[1].lowAlert;
        // that.unusualList.xlList.alertStatus = that.defaultList[1].alertFlag.toString();
      } else if (data == "呼吸率") {
        that.unusualList.hxList.normalHigh = that.defaultList[2].paramOne;
        that.unusualList.hxList.unusualLow = that.defaultList[2].paramTwo;
        that.unusualList.hxList.unusualHigh = that.defaultList[2].paramThree;
        that.unusualList.hxList.alertHigh = that.defaultList[2].highAlert;
        that.unusualList.hxList.alertLow = that.defaultList[2].lowAlert;
        // that.unusualList.hxList.alertStatus = that.defaultList[2].alertFlag.toString();
      } else if (data == "血氧") {
        that.unusualList.xyList.normalLow = that.defaultList[3].paramOne;
        that.unusualList.xyList.unusualLow = that.defaultList[3].paramTwo;
        that.unusualList.xyList.alertLow = that.defaultList[3].lowAlert;
        // that.unusualList.xyList.alertStatus = that.defaultList[3].alertFlag.toString();
      }
    },
    zhuanHuaShe(data) {
      //将摄氏度转为华摄度
      return ((data * 9) / 5 + 32).toFixed(2);
    },
    zhuanhuaHua(data) {
      return ((data - 32) * 5) / 9;
    }
  },
  components: {
    PopUp: popUp
  },
  created() {
    this.selectTems = this.selectTem;
    var transId = "";
    if (!this.selectTems) {
      this.wendu = "(℉)";
    }
    // if (this.transData) {两个设置规则
    //   transId = this.transData.patientId;
    //   getPatientRule({ patientId: transId }).then(res => {
    //     //获取默认值
    //     console.log(res);
    //     if (res.code == 200 && res.success) {
    //       that.defaultList = res.data;
    //     }
    //   });
    // } else {
    //   rule({ departmentId: -1,patientId: transId}).then(res => {
    //     //获取默认值
    //     console.log(res);
    //     if (res.code == 200 && res.success) {
    //       that.defaultList = res.data;
    //     }
    //   });
    // }
    if (this.transData) {
      transId = this.transData.patientId;
      rule({ departmentId: this.departmentId, patientId: "" }).then(res => {
        //获取默认值
        console.log(res);
        if (res.code == 200 && res.success) {
          that.defaultList = res.data;
        }
      });
    } else {
      rule({ departmentId: -1, patientId: transId }).then(res => {
        //获取默认值
        console.log(res);
        if (res.code == 200 && res.success) {
          that.defaultList = res.data;
        }
      });
    }
    this.getRul(transId).then(() => {
      that.accecptDataList = that.rulList;
      console.log(that.rulList);
      that.rulList.map(item => {
        if (item.type == 0) {
          if (!this.selectTems) {
            that.unusualList.twList.normalHigh = Number(
              this.zhuanHuaShe(item.paramOne)
            );
            that.unusualList.twList.unusualLow = Number(
              this.zhuanHuaShe(item.paramTwo)
            );
            that.unusualList.twList.unusualHigh = Number(
              this.zhuanHuaShe(item.paramThree)
            );
            that.unusualList.twList.alertHigh = Number(
              this.zhuanHuaShe(item.highAlert)
            );
            that.unusualList.twList.alertLow = Number(
              this.zhuanHuaShe(item.lowAlert)
            );
            // that.unusualList.twList.alertStatus = item.alertFlag.toString();
          } else {
            that.unusualList.twList.normalHigh = item.paramOne;
            that.unusualList.twList.unusualLow = item.paramTwo;
            that.unusualList.twList.unusualHigh = item.paramThree;
            that.unusualList.twList.alertHigh = item.highAlert;
            that.unusualList.twList.alertLow = item.lowAlert;
            // that.unusualList.twList.alertStatus = item.alertFlag.toString();
          }
        } else if (item.type == 1) {
          that.unusualList.xlList.normalHigh = item.paramOne;
          that.unusualList.xlList.unusualLow = item.paramTwo;
          that.unusualList.xlList.unusualHigh = item.paramThree;
          that.unusualList.xlList.alertHigh = item.highAlert;
          that.unusualList.xlList.alertLow = item.lowAlert;
          // that.unusualList.xlList.alertStatus = item.alertFlag.toString();
        } else if (item.type == 2) {
          that.unusualList.hxList.normalHigh = item.paramOne;
          that.unusualList.hxList.unusualLow = item.paramTwo;
          that.unusualList.hxList.unusualHigh = item.paramThree;
          that.unusualList.hxList.alertHigh = item.highAlert;
          that.unusualList.hxList.alertLow = item.lowAlert;
          // that.unusualList.hxList.alertStatus = item.alertFlag.toString();
        } else if (item.type == 3) {
          that.unusualList.xyList.normalLow = item.paramOne;
          that.unusualList.xyList.unusualLow = item.paramTwo;
          that.unusualList.xyList.alertLow = item.lowAlert;
          // that.unusualList.xyList.alertStatus = item.alertFlag.toString();
        }
      });
    });
  }
};
</script>
<style scoped lang="scss">
.szgz-top {
  display: flex;
  margin: 0 0 15px 75px;
  .szgz-top-item {
    width: 84px;
    height: 34px;
    border-radius: 17px;
    border: 1px solid #1890ff;
    text-align: center;
    line-height: 34px;
    color: #0091ff;
    font-size: 16px;
    margin-right: 25px;
    cursor: pointer;
  }
  .szgz-item-select {
    color: #fff;
    background: #0091ff;
  }
}
.szgz-content {
  flex-direction: row;
  display: flex;
  justify-content: center;
  // margin: 0 42px;
  width: 629px;
  .top {
    display: flex;
    margin: 24px 0 29px;
    justify-content: space-between;
    align-items: center;
    .left {
      display: flex;
      .wendu {
        display: inline-block;
        cursor: pointer;
      }
      .peizhi {
        font-size: 16px;
        color: #232323;
      }
      .danwei {
        color: #a4a9b6;
      }
    }
    .buttun {
      width: 82px;
      height: 27px;
      text-align: center;
      border-radius: 17px;
      line-height: 27px;
      color: #fff;
      background: #1890ff;
      font-size: 14px;
      cursor: pointer;
    }
  }
  .szgz-item {
    padding: 0 40px 0 40px;
    width: 479px;
    background: rgba(255, 255, 255, 1);
    border-radius: 8px;
    border: 2px solid rgba(24, 144, 255, 1);
    box-sizing: border-box;
    margin-bottom: 53px;
    h3 {
      text-align: center;
      font-size: 20px;
      font-family: PingFangSC-Medium, PingFang SC;
      font-weight: 500;
      color: rgba(51, 51, 51, 1);
      margin: 16px 0 6px;
    }
    h6 {
      font-size: 16px;
      font-family: PingFangSC-Medium, PingFang SC;
      font-weight: 500;
      color: rgba(35, 35, 35, 1);
      margin: 0;
      text-align: left;
    }
    .topM {
      margin: 25px 0 15px;
    }
    .szgz-row {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-top: 12px;
      /deep/ .el-input__inner {
        border-bottom: none;
        text-align: left;
        font-size: 16px;
        font-weight: 400;
        color: rgba(51, 51, 51, 1);
        line-height: 22px;
        border: 1px solid #dcdfe6;
      }
      .buttons {
        width: 310px;
        display: flex;
        margin: 0 auto;
        justify-content: space-between;
        padding-bottom: 18px;
        a {
          display: block;
          width: 82px;
          height: 27px;
          background: rgba(24, 144, 255, 1);
          border-radius: 3px;
          font-size: 14px;
          font-family: PingFangSC-Medium, PingFang SC;
          font-weight: 500;
          color: rgba(255, 255, 255, 1);
          line-height: 27px;
          text-align: center;
        }
      }
      em {
        font-style: normal;
        line-height: 22px;
      }
      .input {
        width: 140px;
        padding: 0;
        border: 0;
      }
      .input1 {
        width: 64px;
      }
      span {
        display: block;
        min-width: 38px;
        float: left;
        height: 24px;
        line-height: 24px;
      }
      .szgz-row-left {
        height: 33px;
        line-height: 33px;
        border-radius: 17px;
        font-size: 16px;
        font-family: PingFangSC-Medium, PingFang SC;
        font-weight: 500;
        width: auto;
        min-width: 70px;
        padding: 0px 5px;
      }
      .normal {
        border: 1px solid rgba(226, 226, 226, 1);
        background: rgba(255, 255, 255, 1);
        color: rgba(153, 153, 153, 1);
      }
      .unusual {
        background: #fa9d00;
        border: 1px solid #fa9d00;
        color: rgba(255, 255, 255, 1);
      }
      .high {
        background: rgba(224, 32, 32, 1);
        border: 1px solid rgba(224, 32, 32, 1);
        color: rgba(255, 255, 255, 1);
      }
      .szgz-row-right {
        display: flex;
        flex-direction: column;
        .untwo {
          display: flex;
          flex-direction: row;
          justify-content: flex-end;
          align-items: center;
          em {
            margin: 0 12px;
          }
        }
      }
      .ts-left {
        display: flex;
        align-items: center;
        justify-content: space-around;
        width: 100%;
      }
    }
    .qie-warp {
      display: flex;
      .sheshi {
        height: 27px;
        background: rgba(24, 144, 255, 1);
        border-radius: 17px;
        margin-left: 8px;
        font-size: 14px;
        font-weight: 500;
        color: rgba(255, 255, 255, 1);
        line-height: 27px;
        padding: 0 4px;
        cursor: pointer;
      }
      .danwei {
        font-size: 16px;
        font-weight: 500;
        color: rgba(51, 51, 51, 1);
        line-height: 22px;
      }
      .qiehuan {
        background: rgba(255, 255, 255, 1);
        color: rgba(24, 144, 255, 1);
        border: 1px solid rgba(24, 144, 255, 1);
      }
    }
    .szgz-row1 {
      position: relative;
    }
    .szgz-row2 {
      margin: 41px 0 31px 0;
    }
    .szgz-row3 {
      height: 48px;
    }
    .normal-scope {
      margin: 0 auto 17px auto;
      width: 140px;
      height: 18px;
      a {
        font-size: 14px;
        font-family: PingFangSC-Regular, PingFang SC;
        font-weight: 400;
        color: rgba(24, 144, 255, 1);
        border-bottom: 1px solid rgba(24, 144, 255, 1);
      }
      .scope-img {
