      }
    }
  }
}
.szgz-row /deep/ .el-input__inner {
  border: 0;
  border-bottom: 2px solid #1890ff;
  border-radius: 0;
  text-align: center;
  height: 30px;
  padding: 0 !important;
}
.szgz-row /deep/ .el-switch {
  float: right;
}
.szgz-row /deep/ .el-switch__label--left {
  color: #fff;
  position: absolute;
  left: 27px;
  z-index: -1111;
}
.szgz-row /deep/ .el-switch__label--right {
  position: absolute;
  left: 6px;
  color: #fff;
  z-index: -1111;
}
.szgz-row /deep/ .el-switch__label--right.is-active {
  z-index: 1111;
  color: #fff !important;
}
.szgz-row /deep/ .el-switch__label--left.is-active {
  z-index: 1111;
  color: #9c9c9c !important;
}
.sy-left {
  display: flex;
  align-items: center;
  .font {
    font-size: 16px;
    color: #333;
  }
  .tip {
    font-size: 12px;
    color: #a4a9b6;
  }
}
</style>



