<template>
  <PopUp :flow="xsszStatus">
    <div class="box">
      <div class="container">
        <i class="iconfont close" @click="showSet()">&#xe648;</i>
        <div class="title clearfix">
          <i class="iconfont title-img">&#xe624;</i>
          <span class="title-span">{{ $t("monitor.xxsz") }}</span>
        </div>
        <div class="content">
          <div class="xs">
            <span>{{ $t("monitor.xs") }}：</span>
            <el-select
              class="select"
              style="width:145px"
              v-model="firstBedId"
              filterable
              @change="changeFirst"
              :placeholder="$t('message.select')"
            >
              <el-option
                v-for="item in firstTypeList"
                :key="item.id"
                :label="item.value"
                :value="item.id"
              >
              </el-option>
            </el-select>
            <em>——</em>
            <el-select
              class="select"
              style="width:145px"
              v-model="secondBedId"
              filterable
            >
              <el-option
                v-for="item in secondTypeList"
                :key="item.id"
                :label="item.value"
                :value="item.id"
              >
              </el-option>
            </el-select>
          </div>
        </div>
        <div class="ipt-group clearfix">
          <a href="javascript:void(0)" class="on" @click="chooseBedType()">{{
            $t("button.confirm")
          }}</a>
          <a href="javascript:void(0)" class="off" @click="showSet()">{{
            $t("button.close")
          }}</a>
        </div>
      </div>
    </div>
  </PopUp>
</template>

<script>
import { mapState, mapActions, mapGetters, mapMutations } from "vuex";
import { filterBeds } from "@/api/monitor";
import popUp from "../../components/popUp";
var that = {};
export default {
  name: "tjzc",
  data() {
    return {
      firstBedId: "",
      secondBedId: "",
      firstTypeList: [],
      secondTypeList: [],
      pdType: 0
    };
  },
  props: ["xsszStatus"],
  beforeCreate() {
    that = this;
  },
  computed: {
    ...mapGetters("monitor", ["departmentId"])
  },
  mounted: function() {
    console.log(this.departmentId);
    filterBeds({
      departmentId: this.departmentId,
      bedId: ""
    }).then(res => {
      that.firstTypeList = res.data;
      console.log(res);
    });
  },
  methods: {
    ...mapMutations("monitor", ["setFilterBedId"]),
    ...mapActions("monitor", ["getData"]),
    showSet() {
      this.$emit("closeSz", "显示");
    },
    changeFirst(id) {
      filterBeds({
        departmentId: this.departmentId,
        bedId: id
      }).then(res => {
        that.secondTypeList = res.data;
        console.log(res);
      });
    },
    chooseBedType() {
      if (that.firstBedId > that.secondBedId) {
        that.error(that.$t("monitor.err.xzcwcw"));
      } else if (that.firstBedId && that.secondBedId) {
        console.log(that.firstBedId, that.secondBedId);
        that.setFilterBedId({
          startBedId: that.firstBedId,
          endBedId: that.secondBedId
        });
        that.getData(this.departmentId);
        that.$emit("closeSz", "显示");
      } else {
        that.error(that.$t("monitor.err.xzcwh"));
      }
    }
  },
  components: {
    PopUp: popUp
  }
};
</script>
<style scoped lang="scss">
.content {
  margin: 0 42px;
  flex-direction: column;
  .xs {
    display: flex;
    padding: 109px 89px;
    span {
      display: block;
      line-height: 36px;
      font-size: 16px;
      font-family: PingFangSC-Medium, PingFang SC;
      font-weight: 500;
      color: rgba(51, 51, 51, 1);
      padding-right: 19px;
    }
    em {
      font-style: normal;
      display: block;
      line-height: 36px;
      font-size: 16px;
      font-family: PingFangSC-Medium, PingFang SC;
      font-weight: 500;
      color: #bfbfbf;
      padding: 0 20px;
    }
  }
  .xs /deep/ .el-input__inner {
    height: 36px;
  }
}
</style>



