<template>
  <div class="header">
    <div class="header-warp">
      <div class="left">
        <div v-if="selectData == $t('monitor.header.jiance')">
          <el-tooltip
            effect="dark"
            :content="$t('monitor.header.fangda.name')"
            placement="bottom"
            v-if="!fullScreen"
          >
            <i class="iconfont fangda" @click.stop="requestFullScreen"
              >&#xe61c;</i
            >
          </el-tooltip>
        </div>
        <el-tooltip
          effect="dark"
          :content="$t('monitor.header.fangda.name2')"
          placement="bottom"
          v-if="fullScreen"
        >
          <i class="iconfont fangda" @click.stop="exitFullscreen">&#xe650;</i>
        </el-tooltip>
        <el-tooltip
          effect="dark"
          :content="$t('monitor.header.xianshi.name')"
          placement="bottom"
          v-if="selectData == $t('monitor.header.jiance')"
        >
          <i class="iconfont eyes" @click.stop="showModel('显示')">&#xe624;</i>
        </el-tooltip>
        <el-tooltip
          effect="dark"
          :content="$t('monitor.header.shezhi.name')"
          placement="bottom"
          v-if="selectData == $t('monitor.header.jiance')"
        >
          <i class="iconfont shezhi" @click.stop="showModel('设置')"
            >&#xe622;</i
          >
        </el-tooltip>
        <i
          class="iconfont shezhi"
          @click.stop="theme"
          :class="!this.changeTheme ? 'changeCloro' : ''"
          >&#xe689;</i
        >
      </div>
      <div class="mid">
        <div
          class="mid-item"
          v-for="(item, index) in jianData"
          :key="index"
          @click.stop="selectRouter(item, index)"
          :class="selectData == item.font ? 'changeCloro' : ''"
          :style="item.role >= role ? '' : 'display:none'"
        >
          <div style="display: flex; align-items: center">
            <i
              class="iconfont font2"
              v-html="item.icon"
              :class="selectData == item.font ? 'changeCloro' : ''"
            ></i>
            <div>{{ item.font }}</div>
            <i
              class="iconfont font4"
              :class="item.show ? 'trans' : ''"
              v-if="item.subList.length !== 0"
              >&#xe625;</i
            >
          </div>
          <div v-if="item.subList.length !== 0">
            <transition>
              <div class="mid-bottom" v-show="item.show">
                <div
                  class="mid-bottom-item"
                  v-for="(items, indexs) in item.subList"
                  :key="indexs"
                  :class="selectData == items.font ? 'changeCloro' : ''"
                  @click.stop="selectItemRouter(items)"
                >
                  <i
                    class="iconfont font4"
                    v-html="items.icon"
                    :class="selectData == items.font ? 'changeCloro' : ''"
                  ></i>
                  <div>{{ items.font }}</div>
                </div>
              </div>
            </transition>
          </div>
        </div>
      </div>
      <div class="right" @click.stop="showExit">
        <img src="../../static/image/touxiang.png" class="circle" />
        <i class="iconfont font3" :class="tuichu ? 'trans' : ''">&#xe625;</i>
        <div class="exit" v-show="tuichu" @click="exitAccount">
          <div class="exit-font">
            {{ $t("monitor.header.tuichu") }}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapState, mapActions, mapGetters } from "vuex";
import { logOut } from "../api/login";
import { getToken } from "@/utils/auth";
export default {
  data() {
    return {
      changeTheme: true, //跟换背景参数
      jianData: [
        {
          font: this.$t("monitor.header.jiance"),
          icon: "&#xe633;",
          role: 2,
          subList: [],
          show: false
        },
        {
          font: this.$t("monitor.header.lishi"),
          icon: "&#xe61e;",
          role: 2,
          subList: [],
          show: false
        },
        {
          font: this.$t("monitor.header.shebei"),
          icon: "&#xe62f;",
          role: 1,
          subList: [
            { font: this.$t("monitor.header.jianceShebei"), icon: "&#xe683;" },
            { font: this.$t("monitor.header.luyou"), icon: "&#xe62c;" }
          ],
          show: false
        },
        {
          font: this.$t("monitor.header.xitongShezhi"),
          icon: "&#xe684;",
          role: 1,
          subList: [
            { font: this.$t("monitor.header.zhanghao"), icon: "&#xe630;" },
            { font: this.$t("monitor.header.chuangwei"), icon: "&#xe62d;" }
          ],
          show: false
        },
        {
          font: this.$t("monitor.header.tjgw"),
          icon: "&#xe635;",
          role: 2,
          subList: []
        }
      ],
      fullScreen: false,
      selectData: this.$t("monitor.header.jiance"),
      tuichu: false //是否显示退出
    };
  },
  computed: {
    ...mapState("monitor", ["rotuerName"]),
    ...mapGetters("login", ["useDepartId", "role"])
  },
  methods: {
    ...mapActions("monitor", ["changeRotuerName"]),
    ...mapActions("login", ["logeOut"]),
    requestFullScreen() {
      var el = document.documentElement;

      var rfs =
        el.requestFullScreen ||
        el.webkitRequestFullScreen ||
        el.mozRequestFullScreen ||
        el.msRequestFullScreen;

      if (typeof rfs != "undefined" && rfs) {
        rfs.call(el);
      } else if (typeof window.ActiveXObject != "undefined") {
        //for IE，这里其实就是模拟了按下键盘的F11，使浏览器全屏

        var wscript = new ActiveXObject("WScript.Shell");

        if (wscript != null) {
          wscript.SendKeys("{F11}");
        }
      }
    },
    theme() {
      //切换主题
      this.changeTheme = !this.changeTheme;
      if (this.changeTheme) {
        window.document.documentElement.setAttribute("data-theme", "black");
      } else {
        window.document.documentElement.setAttribute("data-theme", "white");
      }
    },
    exitFullscreen() {
      // 判断各种浏览器，找到正确的方法
      var exitMethod =
        document.exitFullscreen ||
        document.mozCancelFullScreen ||
        document.webkitExitFullscreen ||
        document.webkitExitFullscreen;
      if (exitMethod) {
        exitMethod.call(document);
      } else if (typeof window.ActiveXObject !== "undefined") {
        //for Internet Explorer
        var wscript = new ActiveXObject("WScript.Shell");
        if (wscript !== null) {
          wscript.SendKeys("{F11}");
        }
      }
    },
    selectRouter(item, index) {
      this.jianData.forEach((items, indexs) => {
        if (index !== indexs) {
          items.show = false;
        }
      });
      this.jianData[index].show = !this.jianData[index].show;
      var data;
      if (item.subList.length == 0) {
        data = item.font;
        if (data == this.$t("monitor.header.jiance")) {
          this.$router.push({ path: "/" }).catch(err => {
            err;
          });
        } else if (data == this.$t("monitor.header.lishi")) {
          this.$router.push({ path: "/history" }).catch(err => {
            err;
          });
        } else if (data == this.$t("monitor.header.tjgw")) {
          this.$router.push({ path: "/statistics" }).catch(err => {
            err;
          });
        }
        this.selectData = data;
        this.changeRotuerName(data);
      } else {
      }
    },
    selectItemRouter(items) {
      var data = items.font;
      if (data == this.$t("monitor.header.jianceShebei")) {
        this.$router.push({ path: "/device" }).catch(err => {
          err;
        });
      } else if (data == this.$t("monitor.header.chuangwei")) {
        this.$router.push({ path: "/bed" }).catch(err => {
          err;
        });
      } else if (data == this.$t("monitor.header.luyou")) {
        this.$router.push({ path: "/router" }).catch(err => {
          err;
        });
      } else if (data == this.$t("monitor.header.zhanghao")) {
        this.$router.push({ path: "/account" }).catch(err => {
          err;
        });
      } else if (data == this.$t("monitor.header.tjgw")) {
        this.$router.push({ path: "/statistics" }).catch(err => {
          err;
        });
      }
      this.selectData = data;
      this.changeRotuerName(data);
      this.jianData.forEach((item, index) => {
        item.show = false;
      });
    },
    showExit() {
      this.tuichu = !this.tuichu;
    },
    exitAccount() {
      var that = this;
      var data = {
        token: getToken()
      };
      this.logeOut(data);
    },
    showModel(data) {
      this.$emit("openModel", data);
    }
  },
  props: ["showState"],
  watch: {
    showState(o, n) {
      if (!o) {
        this.jianData.forEach((item, index) => {
          item.show = false;
        });
        this.tuichu = false;
      }
    }
  },
  mounted() {
    var that = this;
    var addEvent =
      "webkitfullscreenchange" ||
      "fullscreenchange" ||
      "mozfullscreenchange" ||
      "msfullscreenchange";
    document.addEventListener(addEvent, function(event) {
      if (document.fullscreenElement) {
        console.log("进入知全屏");
        that.fullScreen = true;
      } else {
        that.fullScreen = false;
        console.log("退出全屏");
      }
    });
  },
  created() {
    console.log(this.rotuerName);
    if (this.rotuerName) {
      this.selectData = this.rotuerName;
    }
  }
};
</script>
<style lang="scss" scoped>
.header {
  height: 47px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(68, 68, 68, 0.5);
  position: relative;
  z-index: 10;
}
.header-warp {
  width: 96%;
  display: flex;
  justify-content: space-between;
  // background: #122540;
  .left {
    width: 160px;
    height: 25px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .mid {
    width: 70%;
    height: 25px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .mid-item {
    font-size: 18px;
    font-weight: 500;
    color: rgba(255, 255, 255, 1);
    line-height: 25px;
    margin-right: 20px;
    cursor: pointer;
  }
  .mid-bottom {
    min-width: 120px;
    background: rgba(68, 68, 68, 0.7);
    position: absolute;
    top: 47px;
    .mid-bottom-item {
      display: flex;
      justify-content: center;
      color: #fff;
      padding: 9px 0;
    }
  }
  .font4 {
    font-size: 8px;
    margin: 0 5px;
  }
  .mid-bottom-item:hover {
    color: rgba(0, 145, 255, 1);
    .font4 {
      color: rgba(0, 145, 255, 1);
    }
  }
  // .mid-item:hover {
  //   color: rgba(0, 145, 255, 1);
  //   .font2 {
  //     color: rgba(0, 145, 255, 1);
  //   }
  // }
  .font2 {
    font-size: 14px;
    margin-right: 10px;
  }
  .changeCloro {
    color: rgba(0, 145, 255, 1) !important;
  }
  .right {
    width: 60px;
    height: 25px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-left: 10px;
    position: relative;
  }
  .circle {
    width: 28px;
    height: 28px;
    border-radius: 50%;
    background: chartreuse;
    margin-right: 10px;
  }
  .font3 {
    font-size: 8px;
    cursor: pointer;
    transition: all 1s;
  }
  .trans {
    transition: all 1s;
    transform: rotate(-180deg);
  }
  .eyes {
    font-size: 14px;
    cursor: pointer;
  }
  .fangda {
    cursor: pointer;
    font-size: 22px;
  }
  .shezhi {
    cursor: pointer;
    font-size: 24px;
  }
}
.exit {
  width: 73px;
  height: 40px;
  background-color: black;
  position: absolute;
  top: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  .exit-font {
    font-size: 14px;
    font-weight: 500;
    color: #fff;
  }
}
</style>



